/********************************************************
  HEADER
********************************************************/
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  height: 60px;
  width: 100%;
  background: linear-gradient(to bottom, rgba(0,0,0,0.43) 0%,rgba(84,84,84,0) 100%);
  z-index: 1;
  .gnb{
    display: none;
    position: absolute;
    top: 53px;
    right: 12px;
    z-index: 1;
    &.on{
      display: block;
    }
  }
  .title_logo{
    font-size: 17px;
    font-weight: normal;
    text-align: center;
    color: white;
    // background: linear-gradient(45deg, #39466b , black);
    a{
      position: relative;
      display: inline-block;
      img{
        height:60px;
      }
    }
  }
  .link_ticket{
    display: block;
    width: 50px;
    height: 49px;
    background: url(https://img.megabox.co.kr/static/mb/images/common/ico/ico-ticket-w.png) no-repeat 50%/28px;
  }
  .btn_gnb{
    width: 60px;
    height: 60px;
    background: url(https://img.megabox.co.kr/static/mb/images/common/ico/ico-burger-w.png) no-repeat 50%/28px;
  }
  
  .list_gnb{
    width: 125px;
    padding: 15px 0;
    border: 2px solid rgba(255, 255, 255, 0.7);
    text-align: center;
    border-radius: 20px;
    box-shadow: inset 0 0 10px #fff;
    background: rgba(255,255,255,.3);
  }
  .item_gnb{
    a{
      display: block;
      width: 100px;
      margin: 5px 0;
      font-size: 13px;
      line-height: 26px;
      font-weight: bold;
      text-align: center;
      border: 2px solid rgba(255, 255, 255, 0.7);
      border-radius: 20px;
      color: #333;
      background: rgba(255,255,255,.7);
      box-shadow: inset 0 0 10px #fff;
    }
  }
}

// sub header

.link_back{
  display: block;
  width: 60px;
  height: 60px;
  background: url(../img/ico_back.png) no-repeat 50%/24px;
}
.title_sub{
  margin: 0 60px;
  font-size: 17px;
  line-height: 60px;
  font-weight: normal;
  text-align: center;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}